import React, { useState } from 'react';
import emailjs from 'emailjs-com';
import styled from 'styled-components';

const StyledContactForm = styled.div``;
const StyledForm = styled.form`
  .button-link {
    ${({ theme }) => theme.mixins.primaryButton};
  }
`;

const ContactForm = () => {
  const [notSent, pending, success, err] = ['notsent', 'pending', 'success', 'err'];
  const [sendStatus, setSendStatus] = useState(notSent);
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm('service_3nbtaa9', 'template_42cdf7y', e.target, 'user_4IF04CZQmiVP6b3AubpTu')
      .then(
        result => {
          console.log(result.text);
          setSendStatus(success);
        },
        error => {
          console.log(error.text);
          setSendStatus(err);
        },
      );

    e.target.reset();
  }

  return (
    <StyledContactForm>
      <StyledForm onSubmit={sendEmail}>
        <input type="hidden" name="contact_number" required />
        <label>Name</label>
        <input type="text" name="name" required />
        <label>Subject</label>
        <input type="text" name="subject" required />
        <label>Email</label>
        <input type="email" name="email" required />
        <label>Message</label>
        <textarea name="message" />
        <input type="submit" value="Send" className="button-link" />
      </StyledForm>
    </StyledContactForm>
  );
};

export default ContactForm;
